import React from 'react';

import Style from './History.module.scss';

export function History(props){
    return(
        <div className={Style.History}>    
            <button aria-label="Clear History" onClick={props.onClickClearHistory} className={props.history.length === 0 ? Style.Hidden : Style.Button}>Clear History</button>
            <ul className={Style.List}>
                {
                    props.history.map(item => {
                        const calculation = String(`${item.firstNumber} ${item.operator} ${item.secondNumber} =`);
                        return(
                            <li className={Style.ListItem} key={item.id}>
                                <h6 className={calculation.length >= 23 ? `${Style.Calculation} ${Style.TextSmallest}` 
                                    : calculation.length >= 11 ? `${Style.Calculation} ${Style.TextSmaller}`
                                        : `${Style.Calculation} ${Style.TextSmall}`}>{calculation}</h6>
                                <h1 className={String(item.total).length >= 17 ? `${Style.Total} ${Style.TextSmaller}` 
                                    : String(item.total).length >= 8 ? `${Style.Total} ${Style.TextSmall}`
                                        : `${Style.Total} ${Style.TextNormal}`}>{item.total}</h1>
                            </li>
                        );
                    })
                }
            </ul>
        </div> 
    );
}